		//选择规格按钮事件
		colorOnClick:function(index){
			this.colorOn =index
			this.colorActive = this.colorList[this.colorOn];
		},
		//增加规格数量
		addNum:function(){
			this.num=parseInt(this.num)+1
		},
		//减少规格数量
		minusNum:function(){
			if(this.num<1){
				this.num = 0
			}else{
