			if(this.num == 0){
				uni.showToast({
					title:"请选择购买数量！",
					icon:"none"
				})
			}else{
				uni.showToast({
					title:"加入购物车成功！",
					icon:"none"
				})
			}
			setTimeout(function(){
				uni.hideToast()
			},1500)
		},
		//侧边栏点击分类事件
		changeItem:function(id,pid){
			this.titleId = id
			if(pid == 1){
				this.lists = this.mainList1.filter(function(item){
					return item.id == id
				})[0].children
			}else{
				this.lists = this.mainList2.filter(function(item){
